.Project {
  background-color: #fff;
  padding: 5rem 0;
}
.container-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
 
}
.container-cards > * {
  flex-basis: 100%;
  /* flex: 1 1 20rem; */
  margin-left: auto;
  margin-right: auto;
}
#card {
  max-width: 30rem;
  font-family: var(--ff-text-card);
  /* box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1); */
  box-shadow: -5px -5px 10px;
}
.card-body {
  background: blanchedalmond;
  display: grid;

}
.btns-card {
display: flex;

}
.bt-card {
  display: inline-block;
  text-decoration: none;
  padding: 0.3rem 1rem;
  background-color: chocolate;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  color: white;
  border-radius: 15px;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1); 
}
.bt-card:hover{
  background-color: var(--txt-color-name);
  color:rgb(26, 218, 218);
}